.listContainer {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .listWrapper {
    width: 100%;
    max-width: 1024px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .listSearch {
    flex: 1;
    background-color: #febb02;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 20px;
  }
  
  .lsTitle {
    font-size: 20px;
    color: #555;
    margin-bottom: 10px;
  }
  
  .lsItem {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 10px;
  }
  
  .lsItem > label {
    font-size: 16px;
    font-weight: 600;
  }
  
  .lsItem > input {
    height: 30px;
    border: none;
    padding: 5px;
  }
  
  .lsItem > span {
    height: 30px;
    padding: 5px;
    background-color: white;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .lsOptions {
    padding: 10px;
  }
  
  .lsOptionItem {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    color: #555;
    font-size: 12px;
  }
  
  .lsOptionText {
    font-size: 16px;
    font-weight: 600;
  }
  
  .lsOptionInput {
    width: 50px;
  }
  
  .listSearch > button {
    padding: 10px;
    background-color: #0071c2;
    color: white;
    border: none;
    width: 100%;
    font-weight: 500;
    cursor: pointer;
  }
  
  .listResult {
    flex: 3;
  }
  
  /* Responsive styles */
  @media (max-width: 768px) {
    .listResult{
    }
    .listWrapper {
        flex-direction: column;
        gap: 10px;
    }
    
    .listSearch {
        display: none;
      margin-bottom: 10px;
    }
  
    .listResult {
      flex: none;
    }
  }